import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';

import './SoldProducts.css';
import SideDrawer from '../AdminPage/components/SideDrawer';
import agent from '../../agent';
import {
  GET_SOLD_PRODUCTS,
} from '../../constants/actionTypes';
import Loader from '../../common/Components/Loader';
import SoldOrder from './components/SoldOrder';

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      user: {},
      isNormalUser: true,
      soldProducts: [],
      error: '',
    };
  }

  componentWillMount() {
    // console.log(this.props);
    if (this.props.user && this.props.user.user) {
      const temp = this.props.user.user.user_type === 'normal';
      this.setState({ user: this.props.user.user, isNormalUser: temp });
    }
    this.props.getSoldProducts();
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        const temp = np.user.user.user_type === 'normal';
        this.setState({ user: np.user.user, loading: false, isNormalUser: temp });
      }
    }
    if (np.soldProducts && this.state.soldProducts !== np.soldProducts) {
      this.setState({ soldProducts: np.soldProducts });
    }
  }

  renderSoldProducts() {
    return this.state.soldProducts.map((p, i) => (
      <SoldOrder order={p} key={i} />
    ));
  }

  render() {
    const { } = this.state.user;
    const {
      loading, soldProducts,
    } = this.state;
    return (
      <div>
        <SideDrawer user={this.state.user} />

        <div className="admin-bg nav-margin   ">

          <h1 className="ma0">Sold products</h1>
          <div>
            {
              this.renderSoldProducts()
            }
          </div>

        </div>
        {
          loading ? <Loader /> : null
        }

      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  soldProducts: state.SoldProductsReducers.soldProducts,
});

const mapDispatchToProps = (dispatch) => ({
  getSoldProducts: () => dispatch({ type: GET_SOLD_PRODUCTS, payload: agent.SoldProducts.getSoldProducts() }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
