/* eslint-disable class-methods-use-this */
/* eslint-disable func-names */
/* eslint-disable no-shadow */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/jsx-no-bind */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Slide from '@material-ui/core/Slide';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import { toast } from 'react-toastify';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import './styles.css';


import agent from '../../../agent';
import {
  GET_SOLD_PRODUCTS,
} from '../../../constants/actionTypes';
import Loader from '../../../common/Components/Loader';
import CollapseTransition from '../../../common/Components/CollapseTransition';


const available_status = ['Order Processed', 'Order Confirmed', 'Shipped', 'Delivered'];

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      mobileView: false,
      user: {},
      isNormalUser: true,
      isSoldProductDetailsDialogOpen: false,
      error: '',
      nextStatusIndex: 1,
    };
  }

  componentWillMount() {
    // console.log(this.props);
    if (this.props.user && this.props.user.user) {
      const temp = this.props.user.user.user_type === 'normal';
      this.setState({ user: this.props.user.user, isNormalUser: temp });
    }
    if (this.props.order) {
      available_status.filter((s, i) => {
        if (s === this.props.order.status) {
          this.setState({ nextStatusIndex: i + 1 });

          return (i + 1);
        }
      });

      this.setState({ order: this.props.order });
    }
  }

  componentWillReceiveProps(np) {
    if (np.user) {
      if (this.state.user !== np.user.user) {
        const temp = np.user.user.user_type === 'normal';
        this.setState({ user: np.user.user, loading: false, isNormalUser: temp });
      }
    }
    if (np.order) {
      available_status.filter((s, i) => {
        if (s === np.order.status) {
          this.setState({ nextStatusIndex: i + 1 });
          return (i + 1);
        }
      });
      this.setState({ order: np.order });
    }
  }

  togelIsSoldProductDetailsDialogOpen() {
    this.setState({ isSoldProductDetailsDialogOpen: !this.state.isSoldProductDetailsDialogOpen });
  }


  progressDelivery(status) {
    const data = {
      item_id: this.state.order.id,
      status,
    };
    this.setState({ loading: true });
    agent.SoldProducts.updateOrderStatus(data).then((res) => {
      console.log(res);
      this.setState({ order: res.data.item });
      if (this.props.soldProducts) {
        const temp = this.props.soldProducts.map((p) => {
          if (p.id === res.data.item.id) {
            return res.data.item;
          }
          return p;
        });
        this.props.setSoldProducts({ orders: temp });
      } else {
        this.props.setSoldProducts({ orders: [res.data.item] });
      }

      this.setState({ loading: false });
    }).catch((err) => {
      console.log(err, err.response);
      if (err.reponse) {
        toast.error(err.response.data.error);
      } else {
        toast.error('Error orccured in updating status!!!');
      }
      this.setState({ loading: false });
    });
  }

  renderCartContents(items) {
    let totalQuantity = 0;
    let totalPrice = 0;

    return (
      <div >
        <div className="cart-details-cart-item total">
          <h3 className="order-header">Product</h3>
          <h3>Size</h3>
          <h3>Quantity</h3>
          <h3>Price</h3>

        </div>
        {
            items.map((c, i) => {
              totalPrice += (c.meta_data.quantity * c.order.disc_price);
              totalQuantity += c.meta_data.quantity;
              const header = `${c.order.name}`;
              return (
                <div className="cart-details-cart-item" key={c.order.id}>
                  <div className="order-header">
                    <div className="flex items-center" onClick={() => { this.setState({ visibleIndex: this.state.visibleIndex === c.order.id ? -1 : c.order.id }); }}>
                      <h3>{header}</h3>
                      <ExpandMoreIcon />
                    </div>
                    <CollapseTransition visible={this.state.visibleIndex === c.order.id}>
                      <div className="details">
                        <p>{`price: ${c.order.price} Rs.`}</p>
                        <p>{`Discounted Price: ${c.order.disc_price} Rs.`}</p>
                        <p>{`Description: ${c.order.description} `}</p>
                      </div>
                    </CollapseTransition>
                  </div>
                  <h3>{c.meta_data.size}</h3>
                  <h3>{c.meta_data.quantity}</h3>
                  <h3>{c.order.disc_price}</h3>
                </div>
              );
            })
        }
        <div className="cart-details-cart-item total">
          <h3 className="order-header">Total</h3>

          <h3 />
          <h3>{totalQuantity}</h3>
          <h3>{totalPrice}</h3>

        </div>
      </div>

    );
  }

  render() {
    const {
      loading, order, isSoldProductDetailsDialogOpen, nextStatusIndex,
    } = this.state;
    const {
      amount, cod, items, meta_data, paid, status,
    } = order;
    const { user_details } = meta_data;
    return (
      <div className="">
        <div className="sold-product-div  pointer " onClick={this.togelIsSoldProductDetailsDialogOpen.bind(this)}>
          <div className="buyes-details   ">
            <b>Buyer Details :  </b>
            <p>
              {`${user_details.name}`}
            </p>
            <p>
              {`${user_details.number}`}
            </p>
          </div>
          <div className="cart-details">
            <b>cart  Details :  </b>
            <p>
              {` Cart contents: ${items.length}`}
            </p>
            <p>
              {` Cart value: ${amount} Rs.`}
            </p>
          </div>
          <div className="cart-details">
            <b>cart  Status :  </b>
            <p>
              {`${status}`}
            </p>
          </div>
        </div>
        <Dialog
          open={isSoldProductDetailsDialogOpen}
          TransitionComponent={Transition}
          keepMounted
          fullScreen
          onClose={this.togelIsSoldProductDetailsDialogOpen.bind(this)}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"

        >
          <div className="dialog-details-top-bar">
            <h2>Order Details</h2>
            <button type="button" onClick={this.togelIsSoldProductDetailsDialogOpen.bind(this)}>
                  Close
            </button>

          </div>
          <DialogContent>
            <div className="dyna-flex bb justify-around">
              <div className="buyer-details">
                <b>Buyer Details:</b>
                <p>
                  {`Customer Name : ${user_details.name}`}
                </p>
                <p>
                  {`Customer phone number: ${user_details.number}`}
                </p>
                <p>
                  {`Customer email : ${user_details.email}`}
                </p>
              </div>
              <div className="buyer-details">
                <b>Delivery Address: </b>
                <p>
                  {`Customer Address : ${user_details.apartment},${user_details.address}, ${user_details.pincode} `}
                </p>
                <p>
                  {`Customer City : ${user_details.city}`}
                </p>
                <p>
                  {`Customer State : ${user_details.state}`}
                </p>
                <p>
                  {`Customer Country : ${user_details.country}`}
                </p>
              </div>
            </div>
            <div className="dyna-flex bb justify-around">
              <h4>{`Payment Mode : ${cod ? 'cash on delivery' : 'online'}` }</h4>
              <h4>{`Paid : ${paid ? 'Paid' : 'Not paid'}` }</h4>
              <h4>{`Product Status : ${status}` }</h4>
            </div>
            <h4>Order Summary</h4>
            <div className="sold-product-details-order-summary">
              {this.renderCartContents(items)}
            </div>
          </DialogContent>
          <DialogActions>
            {
                  nextStatusIndex <= available_status.length - 1
                    ? (
                      <button type="button" className="w-100 color-btn" onClick={() => this.progressDelivery(available_status[nextStatusIndex])}>
                        {' '}
                        {`Mark as ${available_status[nextStatusIndex]}`}
                        {' '}
                      </button>
                    )
                    : <button type="button" className="w-100 color-btn">Product Delivered </button>
              }

          </DialogActions>
          {
            loading
              ? <Loader />
              : null
        }
        </Dialog>
        
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  soldProducts: state.SoldProductsReducers.soldProducts,

});

const mapDispatchToProps = (dispatch) => ({
  setSoldProducts: (soldProducts) => dispatch({ type: GET_SOLD_PRODUCTS, payload: soldProducts }),

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
