import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import './OrderHistory.css';
import SideDrawer from '../AdminPage/components/SideDrawer';
import agent from '../../agent';
import {
  GET_ORDER_HISTORY,
} from '../../constants/actionTypes';
import Tabs from '../../common/Components/Tabs';

import Order from './components/Order';
import ProductOrder from './components/ProductOrder';
import Loader from '../../common/Components/Loader';


class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      user: {},
      planOrders:[],
      productOrders:[],
    };
    this.orderHistory = [];
  }

  componentWillMount() {
    // console.log(this.props);
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
    if (this.props.orderHistory && this.props.orderHistory.orders) {
      this.formatOrderData(this.props.orderHistory.orders)
      this.orderHistory = this.props.orderHistory.orders;
    }
    this.props.getOrders();
  }

  componentWillReceiveProps(np) {
    if (np.orderHistory && np.orderHistory.orders && np.orderHistory.orders !== this.orderHistory) {
      this.formatOrderData(np.orderHistory.orders)
      this.orderHistory = np.orderHistory.orders;
      
    }
  }

  formatOrderData(orders){
    let planOrders = orders.filter( o => o.items[0].order_type === "subscription")
    let productOrders = orders.filter( o => o.items[0].order_type === "product")
    this.setState({planOrders, productOrders})

  }
  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  componentWillUnmount() {
    window.removeEventListener('resize', () => console.log('removed'));
  }

  renderPlansOrders() {
    return this.state.planOrders.map((o, i) => (
      <Order data={o} key={o.id} />
    ));
  }

  renderProductsOrders() {
    return this.state.productOrders.map((o, i) => (
      <ProductOrder data={o} key={o.id} />
    ));
  }
  render() {
    const { loading } = this.state;
    return (
      <div>
        <SideDrawer user={this.state.user} />


        <div className="nav-margin admin-bg">
          <div className="order-history-header">
            <h1>
              Order History
            </h1>
          </div>
          
           <div className="order-history-content">
          <Tabs 
            tabsData={[
              { tabHeading: 'Plans Purchases', components: this.renderPlansOrders.bind(this) },
              { tabHeading: 'Product Purchases', components: this.renderProductsOrders.bind(this) }
              ]} 
          />
         
            

          </div>


        </div>

        {
          loading ? <Loader /> : null
        }
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  orderHistory: state.OrderHistoryReducers.orderHistory,

});

const mapDispatchToProps = (dispatch) => ({
  getOrders: () => dispatch({ type: GET_ORDER_HISTORY, payload: agent.OrderHistory.getOrders() }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
