import React, { Component } from 'react';

import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';

class Example extends Component {
  constructor(props) {
    super(props);
    this.state = {
      doubleClickMode: '',
      className: '',
      style: {},
    };
    this.doubleClick = 'zoomIn'
  }

  componentWillMount() {
    const { src, className, style } = this.props;
    this.setState({ src, className, style });
  }

  render() {
    const { className, style,  } = this.state;
    const { children } = this.props;
    return (
      <TransformWrapper className={className} style={style} doubleClick={{ mode: this.doubleClickMode }}>
        <TransformComponent>
          { children }
        </TransformComponent>
      </TransformWrapper>
    );
  }
}

export default Example;
