import {
  GET_PRODUCTS,
  SET_PRODUCTS,
  GET_SHIPPING_ADDRESS
} from '../../constants/actionTypes';

export default (state = {}, action) => {
  switch (action.type) {
    case GET_PRODUCTS:
      if (state.products) {
        return { ...state, products: action.pageNumber === 1 ? action.payload.products : [...state.products, ...action.payload.products] };
      }
      return { ...state, products: action.payload.products };
    case SET_PRODUCTS:
      return { ...state, products: action.payload.products };
    case GET_SHIPPING_ADDRESS:
        return { ...state, shippingAddress: action.payload.address }
    default:
      return state;
  }
};
